<script lang="ts" context="module">
    import { AccountCode } from 'tigerspend-types';
    import { encodeAAN } from './aan.svelte';

    export interface TigerwatchPrefs {
        aan: number;
        themeDark: boolean;
    }

    function setPrefs(v: TigerwatchPrefs): void {
        localStorage.setItem('prefs', JSON.stringify(v));
    }

    function getPrefs(): TigerwatchPrefs {
        return JSON.parse(
            localStorage.getItem('prefs') ||
                JSON.stringify({
                    aan: encodeAAN([AccountCode.ACCT_VIRTUAL_SUM]),
                })
        );
    }

    export function setAcct(v: boolean, n: AccountCode): void {
        const cur = getPrefs();
        cur.aan &= ~(+v << n); // set bit on/off
        setPrefs(cur);
    }

    export function getAcct(n: AccountCode): boolean {
        return !!(getPrefs().aan & (1 << n));
    }

    export function getThemeDark(): boolean {
        return getPrefs().themeDark;
    }

    export function setThemeDark(v: boolean): void {
        const cur = getPrefs();
        cur.themeDark = v;
        setPrefs(cur);
    }
</script>
