<script lang="ts">
    import { getAcct } from '../../util/prefs.svelte';
    import { AccountCode } from 'tigerspend-types';
    import FormField from '@smui/form-field';
    import Switch from '@smui/switch';
    import Fab, { Label, Icon } from '@smui/fab';

    let rdd = getAcct(AccountCode.ROLLOVER_DINING_DOLLARS);
    let vdn = getAcct(AccountCode.VOLUNTARY_DINING_DOLLARS);
    let sdd = getAcct(AccountCode.STANDARD_DINING_DOLLARS);
    let tbx = getAcct(AccountCode.TIGER_BUCKS);

    export let go: (where: string) => void;

    function save() {
        go('home');
    }
</script>

<main>
    <div>
        <FormField>
            <Switch bind:checked={sdd} />
            <span slot="label">Standard Dining Dollars (meal plan)</span>
        </FormField>
    </div>
    <div>
        <FormField>
            <Switch bind:checked={rdd} />
            <span slot="label">Rollover Dining Dollars</span>
        </FormField>
    </div>
    <div>
        <FormField>
            <Switch bind:checked={vdn} />
            <span slot="label">Voluntary Dining Dollars</span>
        </FormField>
    </div>
    <div>
        <FormField>
            <Switch bind:checked={tbx} />
            <span slot="label">Tiger Bucks</span>
        </FormField>
    </div>

    <div class="fab">
        <Fab on:click={save} extended color="primary">
            <Icon class="material-icons">save</Icon>
            <Label>Save</Label>
        </Fab>
    </div>
</main>

<style>
    .fab {
        position: fixed !important;
        right: 20px;
        bottom: 20px;
    }
</style>
